@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  font-family: 'Poppins', sans-serif;
}
.conatiner {
  display: flex;
  justify-content: space-between;
}
.App {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px 30px; 
  width: 30%; 
  margin: 0 auto;
  height: 90vh;
}
.taskContainer {
  background: #dce5f1;
  display: flex;
  margin-bottom: 10px;
  justify-content: space-between;
  padding: 0 10px;
}
.taskContainer h5 {
  padding: 5px;
  margin: 0;
}
.taskContainer.flag {
  border-left: 5px solid #2b5b9b;
}
.addBtn {
  font-size: 24px;
  position: relative; 
  top: 20px;
  color: #A2416B; 
  cursor: pointer;
}
.addBtn.clr {
  color: #2b5b9b;
}
.deleteTask {
  color: #113564;
  cursor: pointer;
  font-size: 14px;
  position: relative;
  align-self: center;
}
.formStyle {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 2px;
  margin-bottom: 15px;
  overflow: hidden;
}
.saveBtn {
  float: right;
  background: #113564;
  border: none;
  color: #fff;
  padding: 5px 15px;
  border-radius: 3px;
}
h6 {
  margin: 5px 0;
}
footer {
  text-align: center;
  margin-top: 15px;
  position: fixed;
  bottom: 50px;
  width: 30%;
}
footer a {
  color: #113564;
  text-decoration: none;
  cursor: pointer;
}

